.profile__name{
    font-weight: 500;
    font-size: 27px;
    line-height: 33px;
    text-align: left;
    color: #FFFFFF;
    margin: 0 10px 0 0;
     max-width: 189px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden; 
}

@media screen and (min-width: 1280px){
    .profile__name{

        font-size: 42px;
        line-height: 48px;
        max-width: 294px;
        margin: 0 18px 0 0;
         
}
}